$font-stack:    Helvetica, sans-serif;
$primary-color: #333;
$black : #000000;
$white: #ffffff;

@mixin clearfix() {
	*zoom: 1;
	  &:before,
	  &:after {
	    display: table;
	    content: "";
	    line-height: 0;
	  }
	  &:after {
	    clear: both;
	  }
}

body {
  font: 100% $font-stack;
  color: $primary-color;
}

header .header-container {
	width:100%;
	height: 50px;
	nav {
		width: 100%;
		margin-right: 0 !important;
	}
}

header .header {
	background-color: $black;
	.pe-7s-search {
		color: $white;
		font-size:20px;
		line-height: 50px;
		margin: 15 0;
		cursor: pointer;
	}
	.mini-cart {
		height: 100%;
		width: 60px;
		padding-left: 20px;
		margin: 0 0;

		.pe-7s-shopbag {
			font-size: 20px;
			line-height: 50px;
			color: $white;
			cursor: pointer;
		}
	}

	img {
		margin: 0;
		margin-top: 10px;
	}
}

// 2 section 2
.content-home.content_top_home {
	margin: 30px;
}

.onsale-countdown {
	margin: 30px;

	li {
		list-style-type: none;
	}

	.product-item {
		@include clearfix();
		.product-countdown {
			position: relative;
			&:before {
				content: "";
				background: url("/images/img_border.png") top left;
				position: absolute;
				top: 40px;
				left: 0;
				right: 0;
				bottom: 40px;
			}

			&:after {
				background: #fff none repeat scroll 0 0;
			    bottom: 70px;
			    content: "";
			    left: 30px;
			    position: absolute;
			    right: 30px;
			    top: 70px;
			}

			.content-countdown {
				float: none;
				position: relative;
				z-index: 10;
			}

			.product-detail {
				padding-top: 100px;
			}

			span.price-container.price-final_price.tax.weee {
				margin-left: 20px;
				text-decoration: line-through;
			}
		}
	}
}

.count-down {
	#countbox_1 {
		display: inline-block;
	}
	span.countdown-section span.countdown-amount{
		border: 2px solid #79b6c8;
		color: #222;
		display: inline-block;
		font-size: 17px;
		font-weight: 900;
		height: 66px;
		line-height: 60px;
		margin: 0 0 10px;
		min-width: 66px;
		-webkit-border-radius: 100%;
		-moz-border-radius: 100%;
		-ms-border-radius: 100%;
		-o-border-radius: 100%;
		border-radius: 100%;
		text-align: center;

		b {
			margin: 0 auto;
		}
	}

	span#old-price-5 {
		text-decoration: line-through;
		margin: 20px;
	}
}

// 3 .box-new
.box-new {
	.rokan-product-heading.rokan-onsale-heading {
		h2 {
			width: 50%;
			margin: 0 auto;
			text-align: center;
		}
	}

	.short_desc {
		p {
			width: 50%;
			margin: 0 auto;
			text-align: center;
		}
	}
}
